/*        */
.message-item {
  display: flex;
  padding: 0.5rem 0.75rem;
  margin: 0.5rem 0;
  border-radius: 10px;
  max-width: 90%;
  word-wrap: break-word; /*    */
}

/*     ,   */
.message-item.user {
  background-color: #007bff;
  color: #fff;
  align-self: flex-end;
}

/*   */
.message-item p {
  margin: 0;
  font-size: 1rem;
}

/*     */
.message-item.user p {
  text-align: right;
}
