/*    :  Flex  */
.sbn_container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #fdfdfd;
}

/*   (header)     (sticky) */
.sbn_header {
  position: sticky;
  top: 0;
  background-color: #2ecc71;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 10px;
  direction: rtl;
  text-align: right;
}

/*     SupermarketImage */
.sbn_logo {
  width: 70px;
  height: 70px;
  object-fit: contain;
}

/*   */
.sbn_title {
  color: #fff;
  font-size: 18px;
  margin: 0;
  direction: rtl; /* RTL   */
  text-align: right; /*     */
}

/*     (flex: 1)    */
.sbn_list {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

/*  -UL */
.sbn_listUl {
  list-style: none;
  margin: 0;
  padding: 0;
  direction: rtl; /*   -RTL */
  text-align: right; /*    */
}

/*   */
.sbn_listItem {
  margin-bottom: 8px;
  direction: rtl; /*   -RTL */
  text-align: right; /*     */
}

/*     */
.sbn_branchButton {
  display: block;
  width: 100%;
  text-align: right; /*     */
  padding: 10px 15px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
  cursor: pointer;
  background-color: #fff;
  color: #333;
  transition: background-color 0.2s ease;
}

/*   (hover)   */
.sbn_branchButton:hover {
  background-color: #ecf0f1;
}

/*     (active) */
.sbn_branchButton:active {
  background-color: #dcdcdc;
}

/*  Back */
.sbn_footer {
  display: flex;
  justify-content: flex-end; /*      */
  padding: 10px;
  background-color: #fdfdfd;
  border-top: 1px solid #ccc;
}

/*   */
.sbn_backButton {
  background-color: #2ecc71; /*    */
  color: #fff; /*   */
  border: none; /*   */
  border-radius: 4px; /*   */
  padding: 10px 15px; /*   */
  font-size: 16px; /*   */
  cursor: pointer; /*   */
  transition: background-color 0.2s ease; /*  -hover -active */
  margin-left: 10px; /*   (    ) */
}

.sbn_backButton:hover {
  background-color: #27ae60; /*     -hover */
}

.sbn_backButton:active {
  background-color: #1e8449; /*     -active */
}
