.bar-chart-container {
  display: flex;
  flex-direction: row; /*      */
  align-items: flex-end; /*     */
  overflow-x: auto; /*    */
  gap: 20px; /*     */
  padding: 10px;
  white-space: nowrap; /*       */
  background-color: #f9f9f9;
  border-radius: 10px;
  border: 1px solid #ddd;
  scroll-behavior: smooth; /*   */
  direction: ltr; /*      */
}

.bar-chart-container::-webkit-scrollbar {
  display: none; /*    */
}

.bar-chart-wrapper {
  display: flex;
  flex-direction: column; /*        */
  align-items: center;
  cursor: pointer;
  transition: transform 0.5s ease-in-out; /*    */
}

.bar-chart-wrapper:hover {
  transform: scale(1.1); /*      */
}

.bar-value {
  font-weight: bold;
  margin-top: 5px; /*     */
  color: #333;
}

.bar-chart {
  width: 60px !important; /*   -60  */
  background-color: #00c49f;
  transition: all 0.5s ease-in-out; /*    (, , ) */
  border-radius: 5px 5px 0 0; /*      */
}

.bar-chart-wrapper.selected .bar-chart {
  background-color: #008080; /*     */
  transition: background-color 0.5s ease-in-out; /*    */
}

.bar-date {
  margin-bottom: 5px; /*     */
  font-size: 0.9rem;
  color: #555;
  text-align: center; /*    */
}
