.expense-overview {
  position: relative; /*     */
  overflow: hidden; /*    */
  max-width: 100%; /*    */
  width: 100%; /*    */
  height: auto; /*    */
  min-height: 500px; /*      */
  margin: 0 auto; /*    */
  padding: 20px; /*   */
  box-sizing: border-box; /*     */
  background-color: #f5f5f5;
  font-family: Arial, sans-serif;
}

.expense-overview h1 {
  text-align: center;
  font-size: 2rem;
  color: #333;
}
